<template>
  <div class='warp'>
    <Card>
      <div slot='header' class='card-header'>
        <img src="../../images/LOGO.png" alt="" srcset="" width="50%">
      </div>
      <div slot='content' class='card-content'>
        <img src="../../images/present.png" alt="" srcset="">
      </div>
      <div slot='footer' class='card-footer'>
        <h2>{{ card.name }}</h2>
        <span>(内含:尿检卡、检测结果分析、在线健康咨询)</span>
        <mt-button type="primary" slot='footer' @click.native='handleBuy'>立即购买</mt-button>
        <div>
        </div>
      </div>
    </Card>
  </div>
</template>

<style >
.warp {
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  flex-wrap: wrap;
  margin: 2% 5%;
}
.card-header {
  text-align: left;
  padding-bottom: 15%;
}
.card-content {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}
.card-content img {
  width: 100%;
}
.card-footer {
  /* margin-bottom: 2em; */
}
.card-footer span {
  display: block;
  font-size: 0.78em;
  color: rgb(207, 207, 207);
  margin-bottom: 3em;
}
.card-footer .mint-button--normal {
  padding: 0 30px;
}
</style>

<script>
import Card from '@/components/card.vue';
export default {
  name: 'product',
  components: { Card },
  data() {
    return {
      card: {
        name: '健康检测礼包'
      }
    };
  },
  methods: {
    handleBuy() {
      this.$router.push({ name: 'infos', params: { data: this.card.name } });
    }
  }
};
</script>



