<template>
  <div v-show="responseCon">
    <div class="comwarp">
      <div slot='header' class="comtitle">
        <img src="../../images/success.png" alt="" srcset="" width="50px" height="50px" v-show="responseCon">
        <p v-show="responseCon">{{ title }}</p>
      </div>
      <div slot='content' class="comcon" v-show="this.$route.params.msg === '支付成功'">
        <p>卡券编号：{{ orderNo }}</p>
      </div>
      <div slot='footer' class="comfooter" v-show="this.$route.params.msg === '支付成功'">
        <mt-button size="small" type="primary" slot='footer' :disabled="!orderNo?true:false"
          v-clipboard:copy="orderNo"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError">
          复制并前往兑换
        </mt-button>
      </div>
    </div>
  </div>
</template>

<style lang="" scoped>
</style>

<script>
import { getCouponUsingGET } from '@/../client/javascript/vue-api-client.js';
import { wxPay } from '@/utils/weixin.js';
const coupons = COUPONS_URL;
export default {
  data() {
    return {
      title: this.$route.params.msg,
      orderNo: '',
      responseCon: ''
    };
  },
  methods: {
    onCopy: function(e) {
      this.$toast({ message: '复制成功', position: 'top' });
      this.$indicator.open('跳转中...');
      setTimeout(() => {
        if (e.text) {
          this.$indicator.close();
          window.location.href = coupons;
        }
      }, 1000);
    },
    onError: function(e) {
      this.$toast({ message: '复制失败', position: 'top' });
    }
  },
  mounted: function() {
    this.$indicator.open('加载中...');
    if (this.$route.params.msg === '支付成功') {
      let orderNo = { orderNo: this.$store.state.order.orderNo };
      if (orderNo) {
        getCouponUsingGET(orderNo)
          .then(res => {
            this.orderNo = res.data.body;
            this.responseCon = 'show';
            this.$indicator.close();
          })
          .catch(err => {
            console.log(err);
            this.$indicator.close();
          });
      }
    } else {
      if(this.$route.params.msg){
        this.$toast(this.$route.params.msg)
        this.$indicator.close();
      }
    }
  }
};
</script>
<style>
.comwarp {
  background-color: white;
  margin: 2% 5%;
}
.comtitle {
  font-size: 1.4em;
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.comtitle img {
  padding-right: 20px;
}
.comcon {
  margin: 40px;
}
.comcon p {
  font-size: 1.2em;
  color: grey;
}
.comfooter {
  margin: 40px;
}
.comfooter a {
  font-size: 0.8em;
}
</style>



