<template>
  <div :id="this.$route.name === 'record'?appNo:app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return{
      rangeValue: 0,
      app: 'app',
      appNo: 'appNo'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
  position: absolute;
  height: 90%;
  width: 100%;
  right: 0;
  left: 0;
  top: 30px;
  margin: auto;
  background: url('./images/border-back.png') no-repeat;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
#appNo{
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: none;
}
</style>



