<template>
  <div class="recordWarp">
    <h3>【 购买记录 】</h3>
    <ul class="recordlist">
      <li v-for="item in recordlist" :key="item.coupons" :class="item.status === 2?fontGrey:''">
        <p class="resStatus">卡券编号：{{item.coupon}}
          <mt-button v-show="item.status !== 2" size="small" type="primary"
          v-clipboard:copy="item.coupon"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError">复制并前往兑换</mt-button>
        </p>
        <p class="resStatus">卡券礼包：{{item.productName}} X{{item.num}}
          <span v-show="item.status === 2">{{item.statusText}}</span>
        </p>
        <p>购买日期：{{ dateForm(item.orderTime) }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
const coupons = COUPONS_URL;
export default {
  data() {
    return {
      recordlist: this.$store.state.recordlist,
      fontGrey: 'fontGrey'
    };
  },
  methods: {
    dateForm(time) {
      let date = time;
      return date ? date.substring(0, 10) : '';
    },
    onCopy: function(e) {
      this.$toast({ message: '复制成功', position: 'top' });
      this.$indicator.open('跳转中...')
      setTimeout(() => {
        if (e.text) {
          this.$indicator.close()
          window.location.href = coupons
        }
      }, 1000);
    },
    onError: function(e) {
      this.$toast({ message: '复制失败', position: 'top' });
    }
  }
};
</script>

<style>
.recordWarp {
  height: 90%;
  margin: 2% 2%;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
}
.recordlist {
  height: auto;
  overflow-y: scroll;
}
.recordWarp ul {
  padding: 0 10px;
  margin: 0;
}
.recordWarp li {
  list-style-type: none;
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid gainsboro;
}
.recordWarp .mint-button--small {
  padding: 0 5px;
  margin-left: 2px;
  height: 30px;
}
.recordWarp .resStatus {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.recordWarp h3 {
  text-align: left;
}
.recordWarp p {
  text-align: left;
  margin: 10px 0;
  font-size: 1em;
}
.fontGrey {
  color: gainsboro;
}
</style>





