<template>
  <div>
    <!-- <Header></Header> -->
    <div class="paywarp">
      <div slot='header' class="paytitle">{{ title }}</div>
      <div slot='content' class='paycon'>
        <p>您本次将购买康云健康服务礼包&nbsp;&nbsp;<span>x{{ this.$store.state.order.num }}</span></p>
        <p>总金额：<span>{{ this.$store.state.order.toal }}</span>元</p>
      </div>
      <div slot='footer' class='payfooter'>
        <mt-button size="large" type="primary" slot='footer' @click.native='handlePay'>确认支付</mt-button>
      </div>
    </div>
  </div>
</template>

<style lang="" scoped>
</style>

<script>
import Header from '@/container/nav.vue';
import Card from '@/components/card.vue';
import { wxPay } from  '@/utils/weixin.js';
  export default {
    name: 'payment',
    components: { Header, Card },
    data() {
      return {
        title: '支付页面',
        sumprice: null,
        pays: {}
      }
    },
    methods: {
      handlePay(){
        // this.$indicator.open();
        // console.log(this.$store.state.order)
        wxPay(this.$store.state.order.payConfig)
        // this.$indicator.open('加载中...');
        // this.$indicator.close();
        /* setTimeout(() => {
          // this.$router.push('/complied');

        }, 1000); */
      }
    },
    computed: {
      carded() {
        return this.$store.state.carded;
      },
      order() {
        return this.$store.state.order;
      },
      sum() {
        let res = this.$store.state.carded.price * this.$store.state.carded.sum
        return res.toFixed(2);
      }
    },
    mounted: function() {
      console.log(this.$store.state.order)
      // this.pays.mobile = this.$store.state.carded.phone;
      // this.pays.code = this.$store.state.carded.code;
      // this.pays.num = this.$store.state.carded.sum;
    }
  }
</script>
<style>
.paywarp{
  background-color: white;
  margin: 2% 5%;
}
.paytitle{
  font-size: 1.4em;
  padding-top: 30px;
}
.paycon{
margin: 40px 10px;
}
.payfooter{
margin: 40px 10px;
}
.paywarp p {
  text-align: right;
}
.paywarp span {
  color: red;
}
</style>



