<template>
  <div class='userinfoswarp'>
    <!-- <Header></Header> -->
      <p class='usertitle'>{{ title }}</p>
    <div class='userinfoscon'>
      <mt-field label="手机号：" type="tel" v-model.number="phone.data" :state="phone.state"></mt-field>
      <!-- <div class="validatewarp"> -->
        <mt-field label="验证码：" type="number" v-model.number="code.data" :state="code.state" class="validate">
          <mt-button size="small" type="primary" @click.native='handleSendCode' plain :disabled="!show">
            <span v-show="show">获取验证码</span>
            <span v-show="!show" class="count">{{count}} s</span>
          </mt-button>
        </mt-field>

      <!-- </div> -->
      <mt-field label="数量：" type="number" v-model.number="sum.data" :state="sum.state" class="numform"></mt-field>
      <!-- <div class="mint-cell-wrapper">
        <div class="mint-cell-title">
          <span class="mint-cell-text">数量：</span>
        </div>
        <div class="value">
            <i class="plus">
              <img src="../../images/plus.png" alt="" srcset="">
            </i>
             <div class="sum">{{ sum.data }}</div>
            <i class="minus">
              <img src="../../images/minus.png" alt="" srcset="">
            </i>
        </div>
      </div> -->
      <mt-button size="large" type="primary" slot='footer' @click.native='handleSubmit' class='nextbtn'>下一步</mt-button>
    </div>
  </div>
</template>

<style>
.userinfoswarp {
  background-color: white;
  margin: 2% 5%;
}
.userinfoscon {
  /* margin: 40px; */
}
.usertitle {
  font-size: 1.4em;
  padding-top: 20px;
}
.userinfoscon .mint-field .mint-cell-title {
  width: auto;
}
.userinfoscon .mint-cell-value {
  border-bottom: 1px solid rgb(207, 207, 207);
}
/* .validatewarp{
  display: flex;
  flex: 1;
  width: 100%;
  align-items: center;
} */
.validate {
  /* display: inline-flex;
  width: 70%; */
}
.validate .mint-cell-wrapper {
  /* width: 60%; */
}
.userinfoscon .mint-button--small {
  width: 86px;
  font-size: 0.75em;
}
.count {
  color: red;
}
.userinfoscon .mint-cell-wrapper {
  background-repeat: inherit;
  background-image: none;
  /* padding-right: 0; */
}
.validate .mint-field-state {
  /* margin-left: 0; */
}
.nextbtn {
  margin-top: 30px;
}
.value {
  display: flex;
  flex: 1;
  -webkit-box-flex: 1;
}
.numform input {
  text-align: center;
}
</style>

<script>
import Header from '@/container/nav.vue';
import {
  senSmsUsingGET,
  createOrderUsingPOST
} from '@/../client/javascript/vue-api-client.js';

const TIME_COUNT = 60;

export default {
  name: 'userInfos',
  components: { Header },
  data() {
    return {
      title: '用户信息填写',
      code: this.$route.params.data,
      show: true,
      count: '',
      timer: null,
      phone: {
        data: null,
        state: ''
      },
      code: {
        data: null,
        state: ''
      },
      sum: {
        data: 1,
        state: ''
      }
    };
  },
  methods: {
    handleSendCode() {
      if (this.phone.state !== 'success') {
        this.$toast({ message: '请填写手机号', position: 'top' });
      } else {
        senSmsUsingGET({ mobile: this.phone.data }).then(function(res) {
          console.log(res);
        });
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000);
        }
      }
    },
    handleSubmit() {
      if (
        this.phone.state === 'success' &&
        this.code.state === 'success' &&
        this.sum.state === 'success'
      ) {
        this.$indicator.open();
        //生成订单
        createOrderUsingPOST({
          mobile: this.phone.data,
          code: this.code.data,
          num: this.sum.data
        }).then(response => {
          if (response.data.code === 0) {
            this.$store.commit('createOrder', response.data.body);
            setTimeout(() => {
              this.$router.push('/payment');
              this.$indicator.close();
            }, 2000);
          }
        })
        // .catch(error => {
        //   // this.$indicator.close();
        //   // console.log(error)
        //   // this.$toast(error)
        // });
        this.$store.commit('buyCard', {
          sum: this.sum.data,
          phone: this.phone.data,
          code: this.code.data,
          payed: false
        });
      } else {
        this.$toast({ message: '请填写正确', position: 'top' });
      }
    }
  },
  computed: {
    carded() {
      return this.$store.state.carded;
    }
  },
  watch: {
    'phone.data': function() {
      const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
      if (!reg.test(this.phone.data)) {
        this.phone.state = 'error';
      } else {
        this.phone.state = 'success';
      }
    },
    'code.data': function() {
      const reg = 6 && /^\d{6}$/;
      if (!reg.test(this.code.data)) {
        this.code.state = 'error';
      } else {
        this.code.state = 'success';
      }
    },
    'sum.data': function() {
      const reg = /^\d+$/;
      if (!reg.test(this.sum.data)) {
        this.sum.state = 'error';
      } else {
        this.sum.state = 'success';
      }
    },
    deep: true
  },
  mounted: function() {
    // console.log(this.$route.matched);
    // console.log(this.$store.state);
    if (this.sum.data === 1) {
      this.sum.state = 'success';
    }
    if (this.$store.state.carded.phone) {
      this.phone.data = this.$store.state.carded.phone;
      this.sum.data = this.$store.state.carded.sum;
    }
  }
};
</script>



