import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    carded: {
      name: '1',
      sum: null,
      price: 10.00,
      phone: null,
      code: null,
      payed: false
    },
    order: {},
    WXresult: {},
    recordlist: {},
    response: {},
    error: {}
  },
  mutations: {
    buyCard (state, { name, sum, phone, code, payed }) {
      state.carded.name = name
      state.carded.sum = sum
      state.carded.phone = phone
      state.carded.code = code
      state.carded.payed = payed
    },
    createOrder (state, orders) {
      state.order = orders
    },
    WXresult (state, res) {
      state.WXresult = res
    },
    checkrecord (state, res) {
      state.recordlist = res
    },
    paying (state, payed) {
      state.carded.payed = payed
    }
  },
  actions: {}
})

export default store



